.guide {
  padding-top: 150px;
  background: #f6f5f3;

  .inner {
    width: 100%;

    h1 {
      font: bold 35px/1 'DM Sans';
      color: #52493f;
      text-align: center;
      margin-bottom: 30px;
    }
  
    p {
      font: 24px/1 'DM Sans';
      color: #52493f;
      text-align: center;
      margin-bottom: 150px;
    }

    #instruction_first_section {
      width: 100%;
      background: #262613;
      padding: 5%;
  
      .inner {
        width: 100%;
        height: 80vh;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;

        .pic {
          width: 40%;
          height: 100%;

          img {
            width: 100%;
            height: 100%;
            object-fit: cover;
          }
        }

        .txt {
          width: 60%;
          padding: 140px;
          display: flex;
          flex-direction: column;
          justify-content: center;

          h2 {
            font: bold 32px/1 'DM Sans';
            color: #fff;
            margin-bottom: 30px;
            letter-spacing: 1px;
          }

          p {
            font: 15px/1.5 'DM Sans';
            color: #fff;
            margin-bottom: 30px;
            text-align: left;
          }

          a {
            display: inline-block;
            border: none;
            border-radius: 2px;
            background: #fff;
            width: 100px;
            height: 38px;
            font: 14px/38px 'arial';
            text-align: center;
          }
        }
      }
    }

    #instructions_section {
      width: 100%;
  
      article {
        width: 100%;
        padding: 5%;

        .inner {
          width: 100%;
          display: flex;
          justify-content: space-between;
          flex-wrap: wrap;

          .txt {
            width: 50%;
            border-top: 1px solid #262613;
            padding: 30px 50px 0 0;

            h2 {
              font: bold 32px/1 'arial';
              color: #52493f;
              margin-bottom: 50px;
            }

            .wrapper {
              width: 100%;
              display: flex;
              flex-wrap: wrap;
              flex-direction: row;
              justify-content: space-between;

              .con {
                width: 45%;

                h3 {
                  width: 100%;
                  font: bold 15px/1 'arial';
                  color: #52493f;
                  margin-bottom: 15px;
                }

                p {
                  font: 15px/1.5 'arial';
                  color: #52493f;
                  margin-bottom: 15px;
                  text-align: left;
                }

                a {
                  display: inline-block;
                  border: none;
                  border-radius: 2px;
                  background: #fff;
                  width: 100px;
                  height: 38px;
                  font: 14px/38px 'arial';
                  text-align: center;
                }
              }
            }  

            
          }

          .pic {
            width: 40%;

            img {
              width: 100%;
              height: 480px;
              object-fit: cover;
            }
          }
        }

        &:nth-of-type(2) {

          .inner {

            .txt {
              order: 1;
            }
          }
        }
      }
    }

    .pop {
      width: 100%;
      height: 100%;
      position: fixed;
      top: 0;
      left: 0;
      background: rgba(0,0,0,.8);
      padding: 5vw;

      iframe {
        border: none;
        width: 100%;
        height: 100%;
      }

      span {
        position: absolute;
        top: 3vw;
        right: 5vw;
        font: bold 14px/1 'arial';
        color: #fff;
        cursor: pointer;
      }
    }
  }


}